import { sendCode, queryPhoneHasBeenUsed } from '@/network/register.js'
import { isPoneAvailable } from '@/common/index.js'


//
const sendCodeMixin = {
	data() {
		return {
			//input
			userPhone: '',
			//button: disabled
			sendCodeBtn: false,
			//button:  value
			sendCodeBtnVal: '',
			counter: 60,
			//input
			getCode:null,
			//
			interval: null
		}
	},
	methods: {
		//
		cliSendCode() {
			if(isPoneAvailable(this.userPhone)) {
				//
				this.countDown();
				sendCode("?phone=" + this.userPhone).then(res => {
					if(res.status === 200) {
						if(res.data.code === 4000) {
							uni.showToast({
								title: '',
								icon: 'none'
							});
						}else if(res.data.code === 5003) {
							uni.showToast({
								title: '',
								icon: 'none'
							});
						}else if(res.data.code === 2000) {
							this.getCode = res.data.data;
							uni.showToast({
								title: '',
								icon: 'none'
							});
						} else {
							uni.uni.showToast({
								title: '',
								icon: 'none'
							});
						}
						
					} else {
						uni.uni.showToast({
							title: '',
							icon: 'none'
						});
					}
					
					console.log(res)
				}).catch(err => {
					uni.showToast({
						title: '',
						icon: 'none'
					});
				}).finally(() => {
					console.log('finally')
				})
			}else {
				uni.showToast({
					title: '',
					icon: 'none'
				})
			}
		},
		//,
		userPhoneInputDefocus(e) {
			//,url,
			const pages = getCurrentPages();
			
			const value = e.detail.value;
			if(value.length > 0 && value.length === 11) {
				queryPhoneHasBeenUsed(`?phone=${value}`).then(res => {
					if(res.status === 200) {
						console.log(res)
						if(res.data.code === 4003) {
							if(pages[pages.length-1].route === 'components/content/register/register1') {
								uni.showToast({
									title: '',
									icon: 'none'
								})
							}
							
						}else if(res.data.code === 2000) {
							if(pages[pages.length-1].route === 'components/content/changepwd/changepwd1') {
								uni.showToast({
									title: '',
									icon: 'none'
								})
							}
						}
						
					} else {
						uni.showToast({
							title: '',
							icon: 'none'
						})
					}
				}).catch(err => {
					uni.showToast({
						title: '',
						icon: 'none'
					})
				})
			} else {
				uni.showToast({
					title: '11',
					icon: 'none',
					duration: 500
				})
			}
		},
		//
		countDown() {
			this.interval = setInterval(() => {
				if(this.counter>0) {
					--this.counter;
					this.sendCodeBtnVal = this.counter +'s';
					this.sendCodeBtn = true;
				}else if(this.counter === 0) {
					this.sendCodeBtnVal = '';
					this.sendCodeBtn = false;
					this.counter=60;
					clearInterval(this.interval)
				}
				
			}, 1000)
		}
		
	},
	
	onUnload() {
		//
		clearInterval(this.interval)
	}
}





//
const listPopShadeMixin = {
	data() {
		return {
			winSize: {},
			//
			showPop: false,
			//
			showPopList: false,
			//
			popStyle: "",
			//
			popList: ['', '', ''],
			//
			pickerUserIndex: -1
		}
	},
	methods: {
		/*  */
		getWindowSize() {
			uni.getSystemInfo({
				success: res => {
					this.winSize = {
						//
						'width': res.windowWidth,
						'height': res.windowHeight,
						//app
						"statusBarHeight": res.statusBarHeight
					}
					console.log(this.winSize)
				}
			})
		},
		/*  */
		handlerLongtap(e) {
			console.log(e)
			/*  H5 style  Object  */
			let [touches, style, index] = [e.touches[0], "", e.currentTarget.dataset.index]
		
			if(touches.clientY > (this.winSize.height/2) ) {
				
				/* APP */



				
				/*  */

				style = `bottom: ${this.winSize.height-touches.clientY}px; `

			}else {
				style = `top: ${touches.clientY}px; `
			}
			
			if(touches.clientX > (this.winSize.width/2)) {
				style += `right: ${this.winSize.width-touches.clientX}px; `
			}else {
				style += `left: ${touches.clientX}px; `
			}
			
			
			//
			this.popStyle = style
			//index
			this.pickerUserIndex = Number(index)
			//
			this.showPop = true
			this.$nextTick(() => {
				setTimeout(() => {
					//
					this.showPopList = true
				}, 10)
			})
			
		},
		//
		hidePop() {
			this.showPopList = false
			setTimeout(() => {
				this.showPop = false
				//
				this.pickerUserIndex = -1
			}, 250)
		},
		//list
		tapList() {
			if(this.showPop) {
				return
			}
		},
		
		/*  */
		//list,
		moveHandle() {
			this.hidePop()
		}
	},
	beforeMount() {
		this.getWindowSize()
	},
	onUnload() {
		console.log('onUnload')
	}
	
}
export { 
	sendCodeMixin,
	listPopShadeMixin
}